html,
bodyÂ  {
  font-size: 10px;
}

a {
  text-decoration: none;
  color: inherit;
}

h1 {
  font-size: 5rem;
  line-height: 5.5rem;
  font-weight: 700;
}

h2 {
  font-size: 3.5rem;
  font-weight: 700;
}

h4 {
  font-size: 2.5rem;
  line-height: 3rem;
}

h5 {
    font-size: 2rem; 
}

p {
  font-size: 2rem;
  line-height: 3rem;
}

.cursor {
  cursor: pointer;
}

.btn {
  width: 140px;
  height: 42px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 20px;
  font-size: 2rem;
  color: #ffffff;
  border: 0;
}

.purple {
  background-color: #431c5d;
}

.orange {
  background-color: #e05915;
}

.white {
  background-color: #e6e9f0;
}

.light-blue {
  background-color: #bccbde;
}

.red {
  background-color: #e01515;
}

.padding {
  padding: 65px 80px;
}

.dispay-none {
  display: none;
}

.f700 {
  font-weight: 700;
}

.f400 {
  font-weight: 400;  
}

.overflow {
  overflow: hidden;
}

.pageStructure {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
}

.container {
  margin-top: 5vh;
  width: 960px;
  height: 500px;
  display: flex;
}

.half-container {
  width: 50%;
  display: flex;
  flex-direction: column;
}

.half-container {
    color: #431C5D;
}

.text-home-center {
    margin: 40px 0; 
}

.home-right {
    display: flex;
    align-items: center;
}

.home-right img {
    align-self: flex-end;
    height: 500px;
}


.profile-page-image {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  height: 80%;
}

.profile-page-image img {
  height: 302px;
  width: 302px;
  border-radius: 50%;
}

.profile-page-btn {
  display: flex;
  flex-direction: column;
}

.profile-btn-line {
  display: flex;
  margin-top: 20px;
  width: 300px;
  justify-content: space-between;
}

.photo-profile-container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.profile-list li {
  font-size: 2rem;
  margin-top: 20px;
}

.input-edit-profile {
    display: flex;
    flex-direction: column; 
    overflow-x: scroll;
    width: 100%;
    height: 60%;
}
.form-auth {
    margin: 30px 0;
}

.input-auth {
  /* width: 100%; */
  background: none;
  border: 2px solid #bccbde;
  font-size: 2rem;
  padding: 10px 20px;
  color: #431c5d;
  margin-bottom: 15px;
  font-weight: 300;
}

.input-sign-up {
    display: flex;
    flex-direction: column; 
    overflow-x: scroll;
    width: 100%;
    height: 50%;
}

.input-login {
    display: flex;
    flex-direction: column; 
    width: 100%;
}

.form-auth input[type=submit]{
    margin-top: 10px;
}

.input-auth::-webkit-input-placeholder {
  color: #431c5d;
  opacity: 0.4;
}

.link-auth-text {
    display: flex;
    justify-content: center;
    align-self: flex-end;
}

.link-auth {
    font-weight: 700;
}

.logoAuth {
    align-self: flex-end;
}

.logoAuth img{
    width: 280px;
}   

.auth-right {
    justify-content: space-between;
}
/* 
orange: #E05915 
purple: #431C5D
*/

nav {
    font-family: 'Oxygen', sans-serif;  
    font-size: 2rem;
    position: fixed;
    width:100vw;
    display: flex;
    justify-content: center;
    background-color: rgba(255,255,255,1)
}

.navbar h2 {
    font-weight: 700;
    color: #E05915;
    font-size: 2.5rem;
}

.navbar ul {
    display: flex;
    align-items: center;
}

.navbar ul li {
    margin-left:32px; 
    color: #431C5D; 
}

.navbar {
    align-items: center; 
    display: flex;
    justify-content: space-between;
    height: 10vh;
    width: 960px;
}

.link-photo-profile {
    display: flex;
    align-items: center;
}

.link-photo-profile  img{
    height: 30px;
    width: 30px;
    border-radius: 50%;
    margin-left: 8px; 
}
.selector-right{
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.selector-right-container img {
    height: 400px;
}

.price-selector {
    margin-top: 20px;
}
.padding-canvas {
  padding: 40px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.control-structure {
  height: 280px;
  overflow-y: scroll;
}

.controles-box {
  border-radius: 10px;
  font-size: 1.4rem;
  display: flex;
  padding: 10px 20px;
}

.controlRGB {
  display: flex;
  flex-direction: column;
  margin-bottom: 10px;
}

.range-control {
  display: flex;
  justify-content: space-between;
}

.range-control input {
  display: flex;
  width: 300px;
}

.btn-control-box {
  display: flex;
  margin-bottom: 10px;
  align-items: center;
}

.btn-control-box > *{
  margin-right: 10px; 
}

.btn-control {
  width: 70px;
  height: 21px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
  font-size: 1.4rem;
  color: #ffffff;
  border: 0;
}

.input-control {
    background:  #E6E9F0;
    border: 1px solid#431C5D;
    color: #431C5D;
    padding: 3px 5px;
    flex-grow: 2;
}

.bottom{
    margin-bottom: 5px;
}

.text-control-inside {
    display: flex;
    align-items: center;
}

.text-control-inside label {
    margin-right: 5px;
}

.text-control-inside input {
    text-align: right;
    width: 100px;
}

.text-control {
    display: flex;
    justify-content: space-between;  
}

.text-control > * {
    margin-right: 10px; 
}

.text-control select {
    background: #E6E9F0;
    color: #431C5D;
    width: 100px;

}

.text-size-control {
    background:  #E6E9F0;
    border: 1px solid#431C5D;
    color: #431C5D;
    width: 100px;
    padding: 3px 5px;
}

.demo-advisor {
  background-color:rgba(244,89,21, 0.7);
  height: 60px;  
  display: flex;
  align-items: center;
  padding: 0 15px;
  border-radius: 10px; 
}

.label-design-name {
  font-size:2rem;
}

.input-design-name-canvas {
  margin-left: 20px; 
  background: #E6E9F0;
  border: 1px solid #431C5D;
  font-size: 1.4rem;
  padding: 5px 10px;
  flex-grow: 2; 
}

.form-save-design {
  display: flex;
  flex-direction: column;
}

.form-inside-design {
  display: flex;
  align-items: center;
  margin-bottom: 10px 
}
.cart-box {
    flex-direction: column;
    justify-content: space-between;
    color: #431C5D;
}

.cartContainer {
    display: flex;
    flex-direction: column;
    overflow-y: scroll;
    height: 450px;
}

.cart-product {
    display: flex;
    background-color: #BCCBDE;
    border-radius: 10px;
    justify-content: space-between;
    align-items: center;  
    padding: 10px 20px;
    margin-bottom: 10px;
}

.form-cart {
    display: flex;
    align-items: center;
}

.label-design-name {
    font-size: 2rem; 
}

.input-design-name {
    width: 50px;
    text-align: right;
    background: #E6E9F0;
    border: 1px solid #431C5D;
    font-size: 1.4rem;
    padding: 3px 5px;
    margin: 0 5px;
}

.btn-quatity {
    color: #ffffff;
    font-size: 1.4rem;
    height: 30px;
    width: 50px;
    border-radius:5px; 
}

.btn-cart {
    font-size: 2rem;
    color: #ffffff;
    width: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 30px;
    border-radius:5px; 
    border: 0;
}

.cart-toPay {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.nameDesignList {
    width: 35%;
}
